* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  overflow: hidden;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.heading {
  text-transform: uppercase;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande", "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  letter-spacing: 0.1rem;
  font-size: calc((1vw + 1vh) * 2.8);
  position: absolute;
  font-weight: 200;
  text-align: center;
  opacity: 0.9;
  margin-top: 0.2rem;
}
.heading span {
  display: block;
  font-family: Helvetica, sans-serif;
  font-size: calc((1vw + 1vh) * 0.75);
  margin-top: 5px;
}

.gallary {
  width: 30%;
  column-width: 2rem;
  gap: 0.5rem;
  transform: scale(8.2);
  will-change: transform;
  transition: all 1s cubic-bezier(0.075, 1, 0.25, 1);
}
.gallary__item {
  margin-bottom: 0.5rem;
  transform: scale(1.001);
  will-change: transform;
  transition: 0.3s;
}
.gallary__item:hover {
  transform: scale(1.03);
}
.gallary__item img {
  display: block;
  width: 100%;
}

.loaded .gallary {
  opacity: 1;
}
.loaded .heading span {
  opacity: 0;
}

@media (prefers-color-scheme: dark) {
  body {
    background: #000;
  }
  .heading {
    filter: invert(100%);
  }
}